/* Social Media Brand Colors */
/*===== Mixins =====*/
/* grid stuff */
/* Baseline Grid
background-image: linear-gradient(#fff 23px, #00fffc 24px, #fff 0px);
background-size: 1px 24px; /* this is a 24px baseline grid */
/* Column Grid
background-color: gray;
background-image: linear-gradient(90deg, transparent 99%, rgba(255,255,255,.5) 0%);
background-size: 11.1%; /* This is at 6-columns -> just change this # */
/*===== Basic Styles =====*/
/* apply a natural box layout model to all elements */

.overflow-container {
	overflow: hidden;
	position: relative;
	height: auto;
	min-height: 100%;
}

.main {
	background: #38312f;
	padding-bottom: 1.5em;
	margin: 0 auto;
}

.loop-container:after {
	content: ".";
	display: block;
	clear: both;
	visibility: hidden;
	line-height: 0;
	height: 0;
}

/* Images */
.alignright,
.alignleft,
.aligncenter,
.alignnone,
.size-auto,
.size-full,
.size-large,
.size-medium,
.size-thumbnail {
	height: auto;
	line-height: 0;
	max-width: 100%;
	max-width: calc(100% - 48px);
}
.alignright img,
.alignleft img,
.aligncenter img,
.alignnone img,
.size-auto img,
.size-full img,
.size-large img,
.size-medium img,
.size-thumbnail img {
	max-width: 100%;
}

.alignleft {
	float: left;
	margin: 0 1.5em 1.5em 0;
}

.alignright {
	float: right;
	margin: 0 0 1.5em 1.5em;
}

.aligncenter {
	text-align: center;
	margin: 1.5em auto;
	display: block;
	max-width: 100%;
}

.alignnone {
	margin: 1.5em;
}

img {
	height: auto;
	line-height: 0;
}

.singular-attachment img {
	max-width: 100%;
}

.wp-caption-text {
	margin: 0;
	color: #8c8c8c;
}

.wp-caption.aligncenter {
	max-width: none;
	margin: 0 auto;
	width: auto !important;
}

/* Form styles */

textarea {
	max-width: 41.625em;
	overflow: auto;
}

input[type="submit"] {
	background: #38312f;
	color: white;
	max-width: 22.2em;
	min-width: 11.1em;
	padding: 0.8em;
	min-height: 45px;
	outline: none;
	border: none;
	font-family: "raleway", sans-serif;
	font-size: 0.813em;
	line-height: 1.85;
	border-radius: 0;
	-webkit-appearance: none;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
input[type="submit"]:hover,
input[type="submit"]:active,
input[type="submit"]:focus {
	cursor: pointer;
	background: black;
}

input[type="search"] {
	margin-right: 1.5em;
	padding-right: 0.375em;
	/* so safari 'x' doesn't get cut off */
}

/* sticky */
.excerpt.sticky .excerpt-container {
	background: wheat;
}

/* ie image border fix */
a img {
	border: none;
}

.screen-reader-text {
	position: absolute;
	clip: rect(1px 1px 1px 1px);
	/* IE6, IE7 */
	clip: rect(1px, 1px, 1px, 1px);
	overflow: hidden;
	height: 1px;
	width: 1px;
}

.skip-content {
	position: absolute;
	width: 100%;
	background: #110800;
	color: #aaaaaa;
	text-align: center;
	padding: 0.375em;
	top: -4.5em;
	z-index: 19;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 0.08em;
}
.skip-content:active,
.skip-content:focus {
	top: 0;
	color: #ffffff;
}

.search .sticky {
	background: white;
}

.blog .main,
.archive .main,
.search .main {
	width: 88.89%;
	max-width: 1280px;
}
.blog #breadcrumbs,
.archive #breadcrumbs,
.search #breadcrumbs {
	width: 100%;
}

.entry {
	max-width: 1280px;
}

.infinite-loader,
#infinite-handle {
	clear: both;
}

#breadcrumbs {
	margin: 1.5em auto;
	width: 88.88%;
	max-width: 1280px;
	color: #aaaaaa;
}
#breadcrumbs a {
	color: #fff;
}

