	opacity: 0.6;
	-webkit-transition: opacity 0.2s ease;
	transition: opacity 0.2s ease;
}
.site-title span {
	opacity: 0;
	position: absolute;
	top: -99em;
}
.site-title a {
	color: white;
}
.site-title a:link,
.site-title a:visited,
.site-title a:hover,
.site-title a:active,
