.excerpt-container {
	padding: 0 5.55%;
	position: relative;
	z-index: 9;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.excerpt {
	text-align: center;
	background: white;
	overflow: hidden;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	/*margin-bottom: 1.5em;*/
}

.featured-image {
	position: relative;
	padding-bottom: 50%;
	height: 0;
	background-position: center;
	background-size: cover;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	-webkit-transition: -webkit-transform 0.3s ease;
	transition: transform 0.3s ease;
}

.featured-image img {
	max-height: 80%;
	max-width: 90%;
	width: 420px;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	/*padding-top: 20px;*/
}

.hline-separator {
	content: "";
	right: -30px;
	left: -30px;
	margin: auto;
	width: 20%;
	opacity: 0.6;
	border: 2px solid #4d4d4d;
}
.intro-separator {
	width: 50%;
}

.search-separator {
	width: 30%;
}

.hline-separator:last-child {
	border: 0px;
}

.zoom .featured-image-link {
