}

.entry-title {
	font-size: 30px !important;
	font-weight: normal !important;
	font-family: "Lato", "Helvetica Neue", "Helvetica", "Arial", "sans-serif" !important;
}

.entry-header {
	font-weight: normal !important;
	font-family: "Lato", "Helvetica Neue", "Helvetica", "Arial", "sans-serif" !important;
	padding-top: 1em;
}

.entry-content article {
	font-weight: lighter !important;
	font-family: "Lato", "Helvetica Neue", "Helvetica", "Arial", "Sans-serif" !important;
}

#blog_images_ img {
	padding: 35px 0;
